@media only screen and (min-width: 992px) {
    .chart-container {
        margin: 5vh 0vh;
        padding: 0;
        height: 40vh;
        width: 100%;
    }

    #chart {
        height: 100%;
        width: 100%
    }

    #chartDark {
        height: 100%;
        width: 100%
    }

    .ml11 {
        display: none;
        font-family:"SF UI Display Thin";
        font-size: 20px;
        color:#7e7e7e
      }
      
      .ml11 .text-wrapper {
        position: relative;
        display: inline-block;
        padding-top: 0.1em;
        padding-right: 0.05em;
        padding-bottom: 0.15em;
      }
      
      .ml11 .line {
        opacity: 0;
        position: absolute;
        left: 0;
        height: 100%;
        width: 3px;
        background-color: #3c64b1;
        transform-origin: 0 50%;
      }
      
      .ml11 .line1 { 
        top: 0; 
        left: 0;
      }
      
      .ml11 .letter {
        display: inline-block;
        line-height: 1em;
      }


  .loaderWrapper {
    display: none;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  .loader {
    border: 12px solid #dddddd; 
    border-top: 12px solid #b8b8b8; 
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 0.7s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  /* The Modal (background) */
  .modal {
    font-family: "SF UI Display Light";
    display: none;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0, 0, 0); /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s;
  }

  /* Add Animation */
  @-webkit-keyframes animatetop {
    from {
      top: -300px;
      opacity: 0;
    }
    to {
      top: 0;
      opacity: 1;
    }
  }

  @keyframes animatetop {
    from {
      top: -300px;
      opacity: 0;
    }
    to {
      top: 0;
      opacity: 1;
    }
  }

  .modal-header-success {
    padding: 2vh 2vw;
    background-color: #3cb188;
    color: white;
  }

  .modal-header-failed {
    padding: 2vh 2vw;
    background-color: #c75353;
    color: white;
  }

  .modal-header-success-dark {
    padding: 2vh 2vw;
    background-color: #5eaa54a6;
    color: white;
  }

  .modal-header-failed-dark {
    padding: 2vh 2vw;
    background-color: #f56f6fa6;
    color: white;
  }

  .modal-content-dark {
    background-color: #3f3f3f;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    width: 80%; /* Could be more or less, depending on screen size */
  }

  /* Modal Content/Box */
  .modal-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background-color: #fefefe;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
  }

  .modal-body {
    margin-top: 2vh;
    padding: 2vh 2vw;
  }
  /* The Close Button */
  .close {
    color: rgba(255, 255, 255, 0.918);
    float: right;
    font-size: 28px;
    font-weight: bold;
  }

  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }

  #statsFailed,
  #statsSuccess {
    font-family: "SF UI Display Medium";
    font-size: 1.4vw;
  }

  #statsFailedDark,
  #statsSuccessDark {
    font-family: "SF UI Display Medium";
    font-size: 1.4vw;
    color: #cccccc;
  }

  * {
    margin: 0;
  }

  .button {
    background-color: #4caf50; /* Green */
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
  }

  .button1 {
    margin-left: 2vw;
    padding: 10px 30px;
    border: none;
    border-radius: 100px 100px 100px 100px;
    background-color: #868686;
    color: #fefefe;
  }

  .button1:hover {
    background-color: #747474;
    color: fefefe;
  }

  .button2 {
    margin-right: 2vw;
    padding: 10px 30px;
    border: none;
    border-radius: 100px 100px 100px 100px;
    background-color: #3c64b1;
    color: #fefefe;
  }

  .button2:hover {
    background-color: #2f508d;
    color: fefefe;
  }

  .modal-footer {
    margin-top: 4vh;
    display: flex;
    justify-content: space-between;
  }

  .correctDark {
    background-color: rgba(60, 101, 177, 0.4);
  }

