    margin: 0vh 0vw 5vh 0vw;
  }

  #caption {
    font-family: "SF UI Display Thin";
    color: #aa9d9d;
    font-size: 20px;
  }

  #gameWord {
    background-color: transparent;
    height: 95vh;
  }

  #gameTime {
    background-color: transparent;
    height: 95vh;
  }

  #passage,
  #passageTime {
    position: relative;
    vertical-align: text-top;
    /* display: flex; */
    justify-content: center;
    align-items: center;
    text-align: left;
    min-height: 40vh;
    font-family: "SF UI Display Light";
    font-size: 36px;
    line-height: 200%;
