  }

  .charSpanTime {
    white-space: pre-wrap;
  }
  #body,
  #bodyTime {
    overflow: hidden;
    margin: 5vh 20vw;
    background-color: #fbfbfd;
    padding: 3vh;
    border-radius: 20px;
    height: 40vh;
  }

  span {
    margin: 1px;
  }

  .current {
    border-bottom: black, 1px;
    background-color: yellow;
  }

  .correct {
    background-color: #3c65b12a;
  }

  .incorrect {
    background-color: rgba(230, 123, 123, 0.473);
  }

  #inputArea {
    margin-top: -50vh;
    color: white;
    border: white;
    outline: none;
    resize: none;
