  }

  #miniFlex,
  #miniFlexTime {
    display: flex;
    flex-direction: column;
    height: 96px;
    width: 96px;
    font-size: 36px;
    font-family: "SF UI Display Medium";
    align-items: center;
    border-radius: 50%;
    background-color: #3c64b1;
    color: #ffffff;
  }

  #seconds,
  #secondsTime {
    font-size: 14px;
    font-family: "SF UI Display Thin";
  }

  #timer,
  #timerTime {
    margin-top: 13px;
    font-size: 36px;
    font-family: "SF UI Display Medium";
  }
  #speed,
  #speedTime,
  #accuracy,
  #accuracyTime {
    display: flex;
    padding: 0;
    margin: 0;
    font-size: 36px;
    font-family: "SF UI Display Medium";
    color: #3c64b1;
    background-color: #f6f6f7;
    border-radius: 20%;
    height: 96px;
    width: 96px;
    justify-content: center;
