  #options {
    width: 65px;
    height: 34px;
    position: relative;
    /* background-image: url("../images/SwitchBackground.png"); */
  }

  #options input {
    height: 0;
    width: 0;
    opacity: 0;
  }

  #darkIcon {
    position: absolute;
    right: 5px;
    top: 4px;
    height: 23px;
    width: 17px;
    /* background-image: url("../images/darkIcon.png"); */
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 34px;
  }

  .slider::before {
    position: absolute;
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    content: "";
    border-radius: 50%;
  }

  #options input:checked + .slider {
    background-color: #3c64b1;
  }

  #options input:checked + .slider:before {
    -webkit-transform: translateX(31px);
    -ms-transform: translateX(31px);
    transform: translateX(31px);
  }

  a.buttonLogIn:hover {
    color: grey;
    background-color: #ffffff;
    border: 0.1em solid grey;
  }

  #flex-options {
    height: 80vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
  }
  #timeMode,
  #wordMode {
    background-color: #fbfbfd;
    align-items: center;
    justify-content: center;
