    margin-bottom: 2vh;
  }

  #captionTime,
  #captionWord {
    color: #a0a0a0;
    font-family: "SF UI Display Light";
    font-size: 18px;
    margin-bottom: 5vh;
  }

  #buttonTime,
  #buttonWord {
    cursor: pointer;
    background-color: #3c64b1;
    color: #ffffff;
    font-family: "SF UI Display Light";
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    padding: 0.35em 1.2em;
  }

  #divButtonTime,
  #divButtonWord {
    margin-bottom: 5vh;
  }
  #dividerTime,
  #dividerMode {
    margin: auto;
    text-align: center;
    width: 25vw;
  }

  .gameTitle {
    text-align: center;
    font-family: "SF UI Display Bold";
    font-size: 64px;
    color: #3c64b1;
    margin-bottom: 3vh;
    padding-top: 8vh;
  }
}

