    background-color: #f6f6f7;
    border-radius: 20%;
    height: 96px;
    width: 96px;
    justify-content: center;
    align-items: center;
  }

  #time,
  #timeTime,
  #wpm,
  #wpmTime,
  #percent,
  #percentTime {
    padding: 0;
    margin: 0;
    font-size: 14px;
    font-family: "SF UI Display Ultralight";
    color: #3c64b1;
    margin-top: 2vh;
  }
  .navLinks {
    /* display: flex; */
    margin: 2vh 0vh;
    padding: 0;
    justify-content: space-between;
    align-items: center;
    list-style: none;
  }

  .navLinks li {
    display: inline-block;
    padding: 0px 5vw;
    color: #3c64b1;
    font-family: "SF UI Display Light";
    font-size: 16px;
  }

  .buttonMode {
    cursor: pointer;
    text-decoration: none;
    background-color: transparent;
    color: #3c64b1;
    border: none;
  }

  header {
    z-index: 1;
    position: sticky;
    top: 0;
    width: 80vw;
    height: 5vh;
    min-height: 50px;
    background-color: #f4f5f4;
    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 0vw 10vw;
    margin: 0;
  }

  img {
    cursor: pointer;
    max-height: auto;
    width: 5vw;
  }

  button {
    font-size: 25px;
  }

  /* a.buttonTheme{
        cursor: pointer;
        display:inline-block;
        padding:0.35em 1.2em;
        border:0.1em solid #3C64B1;
        border-radius:0.12em;
        box-sizing: border-box;
        text-decoration:none;
        font-family: "SF UI Display Light";
        color:#3C64B1;
        text-align:center;
        transition: all 0.2s;
    } */

  #options {
    width: 65px;
    height: 34px;
    position: relative;
    /* background-image: url("../images/SwitchBackground.png"); */
  }

  #options input {
    height: 0;
    width: 0;
    opacity: 0;
  }

  #darkIcon {
    position: absolute;
    right: 5px;
    top: 4px;
    height: 23px;
    width: 17px;
    /* background-image: url("../images/darkIcon.png"); */
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 34px;
  }

  .slider::before {
    position: absolute;
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    content: "";
    border-radius: 50%;
  }

  #options input:checked + .slider {
    background-color: #3c64b1;
  }

  #options input:checked + .slider:before {
    -webkit-transform: translateX(31px);
    -ms-transform: translateX(31px);
    transform: translateX(31px);
  }

  a.buttonLogIn:hover {
    color: grey;
    background-color: #ffffff;
    border: 0.1em solid grey;
  }

  #flex-options {
    height: 200vh;
    display: grid;
    justify-content: center;
    text-align: center;
    align-items: center;
    overflow: hidden;
    margin-top: 10vh;
  }
  #timeMode,
  #wordMode {
    background-color: #fbfbfd;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    margin-bottom: 10vh;
  }

  #titleTime,
  #titleWord {
    color: #a0a0a052;
    font-family: "SF UI Display Bold";
    font-size: 60vw;
  }

  #subtitleTime,
  #subtitleWord {
    color: #3c64b1;
    font-family: "SF UI Display Bold";
    font-size: 20vw;
    margin-bottom: 2vh;
    position: abosulte;
    left: 0px;
    width: 100vw;
  }

  #captionTime,
  #captionWord {
    color: #a0a0a0;
    font-family: "SF UI Display Light";
    font-size: 14px;
    margin-bottom: 5vh;
  }

  #buttonTime,
  #buttonWord {
    cursor: pointer;
    background-color: #3c64b1;
    color: #ffffff;
    font-family: "SF UI Display Light";
    font-size: 3vw;
    text-align: center;
    text-decoration: none;
    padding: 0.35em 1.2em;
  }

  #divButtonTime,
  #divButtonWord {
    margin-bottom: 5vh;
  }

  #dividerTime,
  #dividerMode {
    margin: auto;
    width: 100vw;
  }

  .gameTitle {
    text-align: center;
    font-family: "SF UI Display Bold";
    font-size: 64px;
    color: #3c64b1;
    margin-bottom: 3vh;
    padding-top: 8vh;
  }

  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }

  .sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #3c65b1ed;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }

  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #f1f1f1e5;
    display: block;
    transition: 0.3s;
  }

  .sidenav a:hover {
    color: #f1f1f1;
  }

  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
}

